
export default class Item extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            editing: false,
            val: ''
        }
        this.handleDoubleClick = this.handleDoubleClick.bind(this);
        this.handleBlur = this.handleBlur.bind(this);
        this.editDone = this.editDone.bind(this);
        this.onKeyDown = this.onKeyDown.bind(this);
    }
    handleDoubleClick() {
        let { todo } = this.props;
        this.setState({
            editing: true,
            val: todo.value
        }, () => { this.refs.editInput.focus(); });
    }
    handleBlur() {
        this.setState({
            editing: false
        });
    }
    onKeyDown(ev) {
        if (ev.keyCode === 13) {
            this.editDone();
        }
        if (ev.keyCode === 27) {
            this.setState({
                editing: false
            });
        }
    }
    editDone() {
        let { todo } = this.props;
        let { val } = this.state;
        todo.value = val;
        this.refs.editInput.blur();
    }
    render() {
        let { handleDoubleClick, handleBlur, onKeyDown } = this;
        let { todo, handleDestroy, toggleItem } = this.props;
        let { editing, val } = this.state;
        return (//注意+和双目运算是从左至右
            <li className={editing ? 'editing' : '' + (todo.hasCompleted ? ' completed' : '')}>
                <div className="view">
                    <input
                        type="checkbox"
                        className="toggle"
                        checked={todo.hasCompleted}
                        onChange={ev => toggleItem(todo)}
                    />
                    <label onDoubleClick={handleDoubleClick}>{todo.value}</label>
                    <button className="destroy" onClick={ev => handleDestroy(todo)}></button>
                </div>
                <input type="text" className="edit"
                    value={val}
                    onChange={
                        ev => { this.setState({ val: ev.target.value }) }
                    }
                    onBlur={handleBlur}
                    onKeyDown={onKeyDown}
                    ref='editInput'
                />
            </li>
        );
    }
}

Item.propTypes = {
    todo: PropTypes.object,
    handleDestroy: PropTypes.func,
    toggleItem: PropTypes.func
};