import React from 'react';


import Footer from 'Footer.js';
import Item from 'Item.js';

require('css/base.css');
require('css/index.css');

// let data = [
//     {
//         id,value,hasCompleted
//     }
// ];

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            todoData: [],
            inputValue: '',
            view: 'all'
        };

        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.handleDestroy = this.handleDestroy.bind(this);
        this.clearAllCompleted = this.clearAllCompleted.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.toggleAll = this.toggleAll.bind(this);
        this.toggleItem = this.toggleItem.bind(this);
        this.changeView = this.changeView.bind(this);

    }
    handleKeyDown(ev) {
        if (ev.keyCode !== 13) return;

        let { todoData, inputValue } = this.state;

        let value = inputValue.trim();
        if (value === '') return;

        let todo = {
            id: new Date().getTime(),
            value: value,
            hasCompleted: false
        };

        todoData.push(todo);
        this.setState({
            todoData,
            inputValue: ''
        });
    }

    handleDestroy(todo) {
        let { todoData } = this.state;

        todoData = todoData.filter(value => {
            return value.id !== todo.id;
        });

        this.setState({
            todoData
        });
    }

    clearAllCompleted() {
        let { todoData } = this.state;
        // todoData = todoData.map(ele => {
        //     if (!ele.hasCompleted) {
        //         return ele;
        //     }
        // });//这种写法会导致报错，暂时不知道为什么(原因：数组长度没有变，不符合条件的变undefined了)
        todoData = todoData.filter(ele => {
            return !ele.hasCompleted;
        });
        this.setState({
            todoData
        });
    }

    handleChange(ev) {
        let inputValue = ev.target.value;
        this.setState({
            inputValue
        });
    }

    toggleAll(ev) {
        let checked = ev.target.checked;

        let { todoData } = this.state;
        todoData.forEach(ele => {
            ele.hasCompleted = checked;
        });
        this.setState({
            todoData
        });
    }

    toggleItem(todo) {
        let { todoData } = this.state;
        todoData.forEach(ele => {
            if (todo.id === ele.id) {
                ele.hasCompleted = !ele.hasCompleted;
            }
        });
        this.setState({
            todoData
        });
    }
    changeView(view) {
        this.setState({
            view
        });
    }

    render() {
        let {
            handleKeyDown,
            handleChange,
            handleDestroy,
            clearAllCompleted,
            toggleAll,
            toggleItem,
            changeView
        } = this;
        let { todoData, inputValue, view } = this.state;

        let leftCount = todoData.length;

        let items = todoData.filter(ele => {
            if (ele.hasCompleted === true) {
                leftCount--;
            }
            switch (view) {
                case 'active':
                    return !ele.hasCompleted;
                    breack;
                case 'completed':
                    return ele.hasCompleted;
                    breack;
                default:
                    return true;
            }
        });
        items = items.map((elem, i) => {
            return <Item
                todo={elem}
                handleDestroy={handleDestroy}
                toggleItem={toggleItem}
                key={i}
            />
        });

        let showClearBut = todoData.length > leftCount;

        return (
            <div>
                <header className="header">
                    <h1>todos</h1>
                    <input type="text" className="new-todo"
                        onKeyDown={handleKeyDown}
                        onChange={handleChange}
                        value={inputValue}
                    />
                </header>
                <section className="main">
                    <input
                        type="checkbox"
                        className="toggle-all"
                        onChange={toggleAll}
                        checked={leftCount === 0 && todoData.length !== 0}
                    />
                    <ul className="todo-list">
                        {items}
                    </ul>
                </section>
                {todoData.length !== 0 ? <Footer {...{
                    leftCount,
                    showClearBut,
                    clearAllCompleted,
                    changeView,
                    view
                }} /> : ''}
            </div>
        );
    }
}

ReactDOM.render(
    <App />,
    document.getElementById('root')
);