export default class Footer extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        let {
            leftCount,
            showClearBut,
            clearAllCompleted,
            changeView,
            view
        } = this.props;
        return (
            <footer className="footer">
                <span className="todo-count">
                    <strong>{leftCount} </strong>
                    <span>item left</span>
                </span>
                <ul className="filters">
                    <li>
                        <a href="#/all"
                            onClick={ev => { changeView('all') }}
                            className={view === 'all' ? 'selected' : ''}
                        >All</a>
                    </li>
                    <li>
                        <a href="#/active" 
                            onClick={ev => { changeView('active') }}
                            className={view === 'active' ? 'selected' : ''}
                        >Active</a>
                    </li>
                    <li>
                        <a href="#/completed" 
                            onClick={ev => { changeView('completed') }}
                            className={view === 'completed' ? 'selected' : ''}
                        >Completed</a>
                    </li>
                </ul>
                {showClearBut ? <button className="clear-completed" onClick={clearAllCompleted}>clear all completed</button> : ''}
            </footer>
        );
    }
}
Footer.propTypes = {
    leftCount: PropTypes.number,
    showClearBut: PropTypes.bool,
    clearAllCompleted: PropTypes.func,
    changeView: PropTypes.func,
    view: PropTypes.oneOf(['all', 'active', 'completed'])
};